module.exports = " <div class=banner v-if=!isChrome>This tool has only been tested on Google Chrome and may not work in other browsers</div> <div class=page> <div class=intro> <p class=headline> Swatchsies let's you compose colour palettes for <a href=https://labs.invisionapp.com/craft>InVision's Craft</a> &mdash; quickly, simply and in the browser. </p> <p> Create your own below using hex codes&hellip; <template v-if=\"colors.length == 0\"><span transition=expand>you'll need at least one color</span></template> </p> </div> <template v-for=\"(index, color) in colors\"> <div class=row transition=expand> <div class=droplet v-bind:style=\"{ backgroundColor: color.hex }\"></div> <input class=hex type=text limit=7 v-model=color.hex :value=color.hex> <label class=color-name>{{color.hex | nameColor}}</label> <button class=remove v-on:click=removeColor(index) tabindex=-1>Remove</button> </div> </template> <button class=add v-on:click=addColor>Add another</button> <button v-if=\"colors.length > 0\" v-on:click=download class=download> Download </button> <button v-if=\"colors.length > 0\" v-on:click=savePalette class=share> Share </button> <div class=footer> <p> A thing by <a href=http://alasdairmonk.com>Alasdair Monk</a> </p> </div> <div class=modal-bg v-if=showModal transition=pop v-on:click=closeModal> <div class=\"modal intro\"> <img src=/static/images/download-img.png width=265 height=274 style=\"margin-left: auto; margin-right: auto; margin-bottom:20px\"> <p class=headline>Your Craft library is downloading</p> <p>Unzip the file, put it somewhere safe<br> and then import it into Craft</p> <button class=woop v-on:click=closeModal>Woop</button> </div> </div> <div class=modal-bg v-if=showShare transition=pop> <div class=\"modal intro\"> <p class=headline>Share the link below</p> <p>Anyone with the link will be able to<br>download and view your palette</p> <input type=\"\" v-model=shareUrl disabled=disabled> <button class=woop v-on:click=closeModal>Got it!</button> </div> </div> </div> ";


